import newsList from '../../components/news_list/news_list.vue'
export default {
	data() {
		return {
			newsList: []
		};
	},
	components:{
		'news-list': newsList
	},
	onLoad() {
		this.getNewsData()
	},
	methods: {
		async getNewsData() {
			const res = await this.$myRequest({
				url: 'api/getnewslist'
			})
			this.newsList = res.data.message
			console.log(this.newsList)
		}
	}
}
